h1 {
  text-align: center;
  margin-bottom: -.5vh;
  font-size: 3vh;
  color: white;
  font-family: 'Montserrat', sans-serif;
}

#controls-container {
  display: flex;
  justify-content: center;

  margin-bottom: 1vh;
}

#controls-container #status,#dimensions,#speed,#generation {
  background-color: gray;
  border: solid 0.5vh black;
}

#controls-container button {
  background-color: white;
  color: black;
  border: .1vh solid #4CAF50;
  font-size: 2vh;
  font-family: 'Roboto', sans-serif;
}

#status button {
  background-color: #4CAF50;
}
#controls-container .selected {
  background-color: #4CAF50;
}

#status {
  margin-right: 1vh;
}

#speed {
  margin-left: 1vh;
}



